import React from "react";
import Footer from "../../Components/Footer";
import Header from "../../Components/Header";
import PageHeader from "../../Components/PageHeader";
import debtorss from "../../assets/img/debtorss.webp";
import handsonme from "../../assets/img/handsonme.webp";

const Debtor = () => {
  return (
    <>
      <Header />
      <PageHeader
        pagename="Debtor And Creditor Representation"
        pagelinkname="Services"
      />

      {/* winning sec starts here */}
      <section className="winning-sec">
        <div className="container">
          <div className="row">
            <div className="col-lg-6">
              <div className="content-wrapper">
                <h3>
                  Creditor <span>Committee Advisors</span>
                </h3>
                <div className="blue-divider"></div>
                <p>
                  When insolvent companies enter a formal Chapter 11 Bankruptcy
                  filing, surprises abound, and disputes develop. A Creditor
                  Committee is formed to represent their collective rights, and
                  the investigation process begins while developing financial
                  strategies
                </p>
                <p>
                  We are able to advise the Creditors Committee throughout the
                  bankruptcy proceedings. The facts are confirmed, and
                  information is collected, analyzed, and assimilated.
                </p>

                <p>
                  There are many benefits in retaining an experienced
                  independent third party to provide financial advice as well as
                  developing a strategy. Retaining the services of Sherwood
                  Partners gives the Creditor Committee a unique advantage,
                  especially in this new age. We offer expertise, reliability,
                  speed, and excellent coordination and communication with the
                  committee. We offer realistic assessments and practical
                  advice,but more important, Sherwood is the leader in
                  monetizing intangible assets, which can deliver the
                  best financial returns to the committee. With these tools, the
                  Creditor Committee is in a better position to effectively
                  evaluate the entire portfolio of the Company's assets and act
                  decisively.
                </p>

                <p>
                  Further more, if IP is involved; we have the expertise to work
                  towards the best outcome. We are able to conduct sales and/or
                  licesnsing opportunities, lend against or even purchase the
                  patents.
                </p>
              </div>
            </div>
            <div className="col-lg-6 align-self-center">
              <div className="img-wrapper img-effect">
                <figure>
                  <img src={debtorss} className="img-fluid" />
                </figure>
              </div>
            </div>
          </div>
        </div>
      </section>
      {/* winning sec ends here */}

      {/* Experience sec starts here */}
      <section className="Experience-sec">
        <div className="container">
          <div className="row">
            <div className="col-lg-6 align-self-center">
              <div className="img-wrapper">
                <figure>
                  <img src={handsonme} className="experience-img" />
                </figure>
              </div>
            </div>
            <div className="col-lg-6">
              <div className="content-wrapper">
                <h3>
                  How We <span>Help</span>
                </h3>
                <p>
                  As an advisor to the Creditor Committees, Sherwood Partners
                  performs a wide variety of functions while working with the
                  debtor:
                </p>
                <ul className="how-ul">
                  <li>
                    Forensic investigation into paper trails of the financial
                    books, corporate records, and document edagreements to
                    as certain facts and priorities and analyze the IP portfolio
                  </li>
                  <li>
                    Debriefing employees and management team on intricacies of
                    specific transactions, operations , and  potential
                    opportunities
                  </li>
                  <li>
                    Recommending changes to succeed with a restructuring, if
                    feasible
                  </li>
                  <li>
                    Monitoring the businesses progress with in the implemented
                    plan
                  </li>
                  <li>
                    Exploring other monetization opportunities of the hard
                    assets as well as the IP
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </section>
      {/* Experience sec ends here */}

      <Footer />
    </>
  );
};
export default Debtor;
